
body {
  background: #f7f8fd;
}
.form-group{
  margin-bottom:5px;
}
.content{
  min-height:100%;
}
.form-error-tip {
  font-size: 12px;
  color: #fd6b91;
  height:18px;
  width: 100%;
  float: left;
  margin-top:5px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.form-loading{
  position: fixed;
  height:100%;
  width: 100%;
  z-index: 99999999;
  top:0;
  left:0;
}
.form-loading .loading-content{
  width: 60px;
  height:60px;
  margin:auto;
  padding:10px 0;
  position: absolute;
  vertical-align: middle;
  text-align: center;
  top:0;
  right:0;
  bottom:0;
  left:0;
  background: #000;
  color:#fff;
  opacity:.85;
  border-radius: 15px;
}
.form-head{
  position: fixed;
  top:0;
  left:0;
  width: 100%;
  z-index: 9999;
  background: #fff;
}



